@media (max-width: 1000px) {
  .cards {
    grid-template-columns: repeat(1, 1fr) !important;
    width: 100% !important;
  }
  .sellingsCont {
    width: 100% !important;
    padding: 0 !important;
    padding-bottom: 3.5rem !important;
  }
  .sellingCard {
    width: 100% !important;
    grid-template-rows: 1fr 1fr !important;
  }
  .expMoreHeadig {
    font-size: 1.4rem !important;
  }
  .sellingsListHeading {
    padding-left: 6px;
  }
  .cardImg {
    padding: 10px !important;
    height: 12.5rem !important;
}
.cardItemName {
    font-size: 1.4rem !important;
}
.cardPriceDetails {
    font-size: 23px !important;
}
.cardDetailMRP {
    font-size: 16px !important;
}
.btnCard {
    padding: 8px 0 !important;
    font-size: 15px !important;
}
}

.sellingsMain {
  border: 1px solid;
  /* background: url("./assests/Images/peak_background.svg"); */
  /* background-size: cover; */
  background: linear-gradient( 90deg, rgb(23, 35, 93), rgb(39 112 124), rgb(65, 125, 182) );
  min-height: 93vh;
}

.sellingsCont {
  /* border: 1px solid; */
  min-height: 15rem;
  width: 90%;
  margin: auto;
  padding: 1.5rem;
}

.cards {
  margin: 0.3rem auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 1rem;
  /* border: 1px solid; */
  padding: 5px;
  width: 95%;
  justify-items: center;
  align-items: center;
}
.sellingCard {
  width: 90%;
  border: 1px solid;
  height: max-content;
  display: grid;
  grid-template-rows: 1fr 1fr;
  row-gap: 4px;
  background: #f9f9f9;
  padding: 5px;
  border-color: #9a9a9ac7;
  border-radius: 8px;
  box-shadow: 2px 8px 8px 0px rgb(31 38 135 / 57%);
  border: 1px solid rgba(255, 255, 255, 0.25);
}

.cardImg {
  padding: 5px;
  height: 15rem;
  justify-self: center;
  align-self: center;
}
.cardInfo {
  border-radius: 5px;
  padding: 5px 5px 2.5rem 5px;
  /* border: 1px solid blue; */
  width: 96%;
  justify-self: center;
  position: relative;
}

.cardItemName {
  font-size: 1.5rem;
  font-family: "Questrial", sans-serif;
  padding-bottom: 5px;
}
.cardPriceDetails {
  font-size: 24px;
  /* border: 1px solid; */
  font-family: "Roboto", sans-serif;
}

.cardDetailMRP {
  text-decoration: line-through;
  color: rgb(165, 9, 9);
  margin-left: 0.6rem;
  font-size: 18px;
  font-family: "Roboto", sans-serif;
}

.cardListDesc {
  font-size: 1rem;
  padding-bottom: 4px;
}

.btns {
  display: grid;
  position: absolute;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  left: 0;
  bottom: 0;
  column-gap: 7px;
}

.btnCard {
  padding: 10px 0;
  font-size: 17px;
  border-radius: 3px;
  color: #f9f9f9;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.3s;
}

.btnDelete {
  background: #232528;
}

.btnDelete:hover {
  color: #232528;
  background: #f9f9f9;
  border-color: #232528;
}

.btnEdit {
  background: #368f8b;
}

.btnEdit:hover {
  color: #368f8b;
  background: #f9f9f9;
  border-color: #368f8b;
}
