@media (max-width: 786px) {
  .mainContainer {
    min-height: 85vh !important;
    grid-template-columns: 1fr !important;
    width: 98% !important;
  }
  .mascot {
    display: none !important;
  }
  .signUpContainer {
    position: unset !important;
    padding-bottom: 5rem !important;
    height: max-content !important;
  }
  .signUpHeading {
    font-size: 1.2rem !important;
  }
  .pageHeading {
    font-size: 1.15rem !important;
  }

  .inputsSignUp {
    padding: 0.5rem 0.25rem !important;
  }
  .navbar {
    background: transparent !important;
  }
  .seachbar {
    background: transparent !important;
  }
  .detailsWarning {
    display: none;
  }
  .info i {
    font-size: 1rem !important;
  }
  .info p {
    font-size: 0.8rem !important;
  }
  .moMasCont {
    display: flex !important;
  }
  .singUpForm {
    margin-top: 10px;
  }
  .loginMainContainer {
    min-height: 53vh !important;
    grid-template-columns: 1fr !important;
    width: 98% !important;
  }
  .alreadyUserMobile {
    display: flex !important;
  }
  #signUpAvatar {
  width: 20% !important;
}
.halfWidth {
  margin: 0.8rem auto !important;
}

}

.signUpContainer {
  width: 100%;
  height: 100vh;
  /* background: url(https://images.unsplash.com/photo-1513077202514-c511b41bd4c7?crop=entropy&cs=tinysrgb&fit=crop&fm=jpg&h=900&ixid=MnwxfDB8MXxyYW5kb218MHx8c3R1ZHl8fHx8fHwxNjYwMzg4MTc1&ixlib=rb-1.2.1&q=80&utm_campaign=api-credit&utm_medium=referral&utm_source=unsplash_source&w=1600);
  background-size: cover; */
  position: fixed;
  top: 0;
  left: 0;
  /* z-index: -1; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainContainer {
  width: 70%;
  min-height: 72vh;
  border-radius: 3px;
  display: grid;
  grid-template-columns: 2.5fr 2fr;
  box-shadow: -4px 7px 25px -10px;
}

.signUpFormContainer {
  background: #eaeaea;
  /* border: 1px solid; */
  height: 100%;
  padding: 0.5rem 1rem;
  position: relative;
}

.signUpHeading {
  font-family: "Montserrat", sans-serif;
  color: #232528;
}
#progress {
  position: absolute;
  width: 35%;
  height: 3px;
  background: #275da0;
  top: 0;
  left: 0;
  transition: all cubic-bezier(0.55, 0.085, 0.68, 0.53) 0.7s;
  /* border-radius: 10px; */
}

#next {
  right: 0.5%;
}
#previous {
  left: 0.5%;
  display: none;
}

.navigate:hover {
  color: #275da0;
  border-color: #275da0;
  background: transparent;
}
.navigate:active {
  transform: scale(0.95);
}

.navigate {
  height: 3rem;
  width: 3rem;
  background: #275da0;
  color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.7rem;
  position: absolute;
  bottom: 0.5%;
  cursor: pointer;
  border: 2px solid transparent;
  transition: all 0.3s;
  z-index: 2;
}

.mascot {
  background: #232528;
  /* border: 1px solid; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  user-select: none;
}

.mascotImg {
  width: 130%;
}

.loginMascotImg {
  width: 100%;
}

.mascotHeading {
  font-family: "Montserrat", sans-serif;
  color: white;
  position: absolute;
  top: 7%;
  font-size: 1.8rem;
}

.alreadyUser {
  padding: 5px 10px;
  font-family: "Montserrat", sans-serif;
  color: white;
  position: absolute;
  bottom: 0%;
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.alreadyUserMobile {
  padding: 5px 10px;
  font-family: "Montserrat", sans-serif;
  color: #232528;
  font-size: 1rem;
  justify-content: end;
  align-items: center;
  width: 100%;
  font-weight: bold;
  display: none;
}

.alreadyUserMobile>a {
  color: #232528;
  text-decoration: underline;
}

.alreadyUser button {
  font-size: 0.95rem;
  padding: 4px 13px;
  border-radius: 2px;
  border: 0.1px solid transparent;
  cursor: pointer;
  transition: all 0.3s;
  font-family: "Montserrat", sans-serif;
  color: #232528;
  font-weight: bold;
  background: #eaeaea;
}

.alreadyUser button:hover {
  background: transparent;
  color: white;
  border-color: white;
}

.pageHeading {
  font-size: 1.5rem;
  font-weight: bold;
  color: #515151;
}

.pages {
  margin-top: 0.8rem;
  transition: all cubic-bezier(0.39, 0.575, 0.565, 1) 0.3s;
  position: absolute;
  height: 100%;
  width: 100%;
}

#page_1 {
  opacity: 1;
}
#page_2 {
  opacity: 0;
  z-index: -1;
}
#page_3 {
  opacity: 0;
  z-index: -1;
}

.inputsSignUp {
  padding: 0.5rem 1rem;
}

.info {
  padding: 0.5rem 1rem;
  color: #275da0;
  font-family: "Montserrat", sans-serif;
  display: flex;
  align-self: center;
}

.info i {
  font-size: 1.1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.info p {
  font-size: 0.9rem;
  margin-left: 5px;
  font-weight: 600;
}

.seeSignUpPassword {
  position: absolute;
  font-size: 14px;
  top: 56%;
  transform: translateY(-50%);
  right: 1%;
  cursor: pointer;
}

.fullWidth {
  width: 100%;
  background: #f3f3f3;
}

.halfWidth {
  width: 50%;
  margin: 0rem auto;
}

.singUpForm {
  position: relative;
}

#signUpBtn {
  display: none;
}
.signupLoginBtns {
  font-size: 1rem;
  padding: 5px 18px;
  border-radius: 2px;
  border: 0.1px solid transparent;
  cursor: pointer;
  transition: all 0.3s;
  font-family: "Montserrat", sans-serif;
  background-color: #232528;
  font-weight: bold;
  color: #f9f9f9;
  position: absolute;
  bottom: 1%;
  right: 1%;
  z-index: 2;
}
/* #loginButton {

} */

.signupLoginBtns:hover {
  color: #232528;
  background: transparent;
  border-color: #232528;
}

#signUpAvatar {
  width: 17%;
  border: 1px solid rgba(0, 0, 0, 0.39);
  border-radius: 4px;
  /* transition: all 0.2s; */
}

.dpHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}

.invert {
  color: #232528;
  font-weight: bold;
  background: #eaeaea;
}
.invertContainer {
  margin: 1.3rem auto;
  background: #232528;
}

.flexRelative {
  display: flex;
  position: relative;
  width: 100%;
}

.detailsWarning {
  right: 2%;
  width: max-content;
  font-size: 0.9rem;
}

.moMasCont {
  /* display: flex; */
  justify-content: center;
  align-items: center;
  display: none;
  height: 20%;
}

#mobileMascot {
  width: 80%;
}

.loginMascot {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
}

#mobileLoginMascot {
  width: 90%;
}

.center {
  text-align: center;
}

.loginMainContainer {
  width: 70%;
  min-height: 70vh;
  border-radius: 3px;
  display: grid;
  grid-template-columns: 2.5fr 2fr;
  box-shadow: -4px 7px 25px -10px;
}
