@media (max-width: 1000px) {
  .listItem {
    width: 100% !important;
    grid-template-columns: 1fr 1.5fr !important;
    align-items: center;
    min-height: 11rem !important;
  }
  .itemName {
    font-size: 1.15rem !important;
  }
  .listDetails {
    font-size: 0.85rem !important;
  }
  .listDesc {
    font-size: 0.75rem !important;
  }
  .listItemImageDiv {
    height: 11rem !important;
  }
  .listItemContent {
    padding: 0.7rem 0.5rem !important;
  }
  .listItemsContainer {
    padding-bottom: 3.5rem !important;
  }
  .wishlistHeading {
    width: 100% !important;
    padding: 0 5px;
    font-size: 1.5rem !important;
  }
  .wishlistHeading > i {
    font-size: 1.25rem !important;
  }
  .emptyWishlist {
    height: 30vh !important;
  }
  .searchResultsHeading {
    font-size: 1.7rem !important;
  }
}

.searchResultsHeading {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1rem;
    color: rgb(249, 249, 249);
    padding-top: 0.6rem;
  }
.emptyWishlist {
  text-align: center;
  height: 75vh;
}

@import url("https://fonts.googleapis.com/css2?family=Arima&display=swap");
.wishlistHeading {
  /* text-align: center; */
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: #f9f9f9;
  letter-spacing: 1px;
  font-family: "Arima", cursive;
  width: 90%;
  margin: auto;
}

.wishlistHeading > i {
  font-size: 1.4rem;
}

.listItemsContainer {
  min-height: 95vh;
  padding-bottom: 0.5rem;
  /* padding-top: 1rem; */
  background: url("../src/assests/Images/wave_background2.png");
  background-size: cover;
}

.listItem {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.7rem;
  display: grid;
  grid-template-columns: 1fr 3fr;
  min-height: 16rem;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 2px 8px 8px 0px rgb(31 38 135 / 37%);
  cursor: pointer;
}

.listItem:hover {
  /* border: 0.5px solid; */
  box-shadow: 1px 1px 7px 0px;
  opacity: 0.95;
}

.listItemImageDiv {
  /* border: 1px solid; */
  height: 16rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f9f9f9;
  border-radius: 4px 0px 0px 4px;
}

.listItemImage {
  height: 85%;
}

.listItemContent {
  /* border: 1px solid; */
  padding: 1rem 0.75rem;
}

.itemName {
  font-size: 1.65rem;
  font-family: "Questrial", sans-serif;
  padding-bottom: 5px;
}

.listDetails {
  font-size: 1rem;
  color: #3d3d3d;
}
.listDesc {
  font-size: 1.15rem;
  padding-bottom: 4px;
}
