@media (max-width: 1000px) {
  #bg {
    height: 33vh !important;
  }
  #submarine {
    top: 39% !important;
    height: 11vh !important;
    animation: submraineMoveMobile 2.5s 1 forwards !important;
  }
  #fish_1 {
    height: 3vh !important;
  }
  #fish_2 {
    height: 3.5vh !important;
  }
  @keyframes submraineMoveMobile {
    0% {
      left: 39%;
    }
    100% {
      left: 15%;
    }
  }
}

#bg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 80vh;
}

#submarine {
  position: absolute;
  top: 37%;
  left: 40%;
  transform: translate(-50%, -50%);
  height: 35vh;
  z-index: 1;
  animation: submraineMove 2.5s 1;
  animation-fill-mode: forwards;
}

#fish_1 {
  position: absolute;
  top: 64%;
  left: 80%;
  transform: translate(-50%, -50%);
  height: 7vh;
  z-index: 1;
  animation: fish_1_move 12s linear infinite;
  animation-fill-mode: forwards;
}

#fish_2 {
  position: absolute;
  top: 51%;
  left: 30%;
  transform: translate(-50%, -50%);
  height: 7vh;
  z-index: 1;
  animation: fish_2_move 15s linear infinite;
  animation-fill-mode: forwards;
}

@keyframes submraineMove {
  0% {
    left: 39%;
  }

  100% {
    left: 23%;
  }
}

@keyframes fish_1_move {
  0% {
    top: 64%;
    left: 75%;
    transform: rotateY(0deg);
  }
  45% {
    left: 40%;
    top: 63.8%;
    transform: rotateY(0deg);
  }
  50% {
    left: 40%;
    transform: rotateY(180deg);
  }
  95% {
    top: 64%;
    left: 75%;
    transform: rotateY(180deg);
  }
  100% {
    top: 64%;
    left: 75%;
    transform: rotateY(0deg);
  }
}
@keyframes fish_2_move {
  0% {
    top: 51%;
    left: 30%;
    transform: rotateY(0deg);
  }
  45% {
    left: 70%;
    top: 45%;
    transform: rotateY(0deg);
  }
  50% {
    left: 70%;
    transform: rotateY(180deg);
  }
  95% {
    top: 51%;
    left: 30%;
    transform: rotateY(180deg);
  }
  100% {
    left: 30%;
    transform: rotateY(0deg);
  }
}
