@media (max-width: 1000px) {
  .expMoreContainer {
    width: 100% !important;
    padding-bottom: 2.5rem !important;
  }
  .activities {
    grid-template-columns: repeat(1, 1fr) !important;
    row-gap: 0.4rem !important;
  }
  .activitiesDivs {
    background: rgb(255, 255, 255) !important;
    box-shadow: 1px 1px 0px !important;
  }
  .actImg {
    height: 4rem !important;
  }
  .parasHead {
    font-size: 16px !important;
  }
  .parasContent {
    font-size: 14px !important;
  }
  .paras {
    padding: 8px !important;
  }
  .loginSecurity {
    height: 6.5rem !important;
  }
  .userProfileInfo > p {
    font-size: 1.2rem !important;
  }
  .visitProfile {
    font-size: 12px !important;
  }

  .expMoreBtn {
    font-size: 0.8rem !important;
    padding: 4px 8px !important;
  }
  .message {
    font-size: 14.5px !important;
  }
  .time {
    font-size: 12px;
  }
  .userProfileInfo {
    padding: 0.5rem !important;
  }
  .noNotifs > p {
    font-size: 17px !important;
  }
  .noNotifs > small {
    font-size: 15px !important;
  }
}

.expMoreContainer {
  width: 65%;
  margin: auto;
  margin-top: 1.5rem;
  padding: 0.5rem;
  /* border: 1px solid; */
}

@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap");
.expMoreHeadig {
  font-family: "Montserrat", sans-serif;
  /* margin-bottom: 1rem; */
  font-size: 1.8rem;
}
.loginSecurity {
  margin-top: 0.2rem;
  margin-bottom: 2rem;
  height: 8rem;
  /* border: 1px solid #abababc9; */
  border-radius: 7px;
  display: flex;
  /* background: linear-gradient(350deg, #b6e0ff, #f9f9f9); */
  background: url("../src/assests/Images/wave3.svg");
  background-size: cover;
}

.profilePic {
  height: 100%;
  border-radius: 7px 0px 0px 7px;
}

.userProfileInfo {
  /* border: 1px solid; */
  width: 100%;
  padding: 0.8rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
}

@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap");
.userProfileInfo > p {
  color: #232528;
  font-family: "Nunito", sans-serif;
  font-size: 1.5rem;
  /* font-weight: bold; */
}

.visitProfile {
  color: #4a4a4a;
  font-size: 14px;
}
.fa-angle-right {
  font-size: 12px;
  margin-left: 0.4rem;
}

.expMoreBtn {
  padding: 4.5px 12px;
  border-radius: 6px;
  border: 0.1px solid transparent;
  cursor: pointer;
  transition: all 0.3s;
  margin-right: 10px;
  font-size: 1rem;
}

.expMoreBtn:hover {
  background: transparent;
}

.expMoreBtn > a {
  color: white;
  transition: all 0.3s;
}

.expMoreLogOut {
  background: #d64550;
}

.expMoreLogOut:hover {
  border-color: #d64550;
}

.expMoreLogOut:hover > a {
  color: #d64550;
}

.expMoreLogin {
  background: #1072a6;
}

.expMoreLogin:hover {
  border-color: #1072a6;
}

.expMoreLogin:hover > a {
  color: #1072a6;
}

.expMoreSignUp {
  background: #232528;
}

.expMoreSignUp:hover {
  border-color: #232528;
}

.expMoreSignUp:hover > a {
  color: #232528;
}

.subTitles {
  font-size: 18px;
  margin-top: 1rem;
  position: relative;
}

.activities {
  margin-top: 0.2rem;
  margin-bottom: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 0.8rem;
  row-gap: 0.8rem;
  transition: all 0.1s;
}

.activitiesDivs:hover {
  background: rgb(226, 226, 226);
}

.activitiesDivs {
  border: 0.05px solid #b4b4b4;
  border-radius: 8px;
  display: grid;
  grid-template-columns: 1fr 3fr;
  height: 7rem;
  padding: 0.3rem;
  /* background: rgb(255, 255, 255); */
  color: #232528;
  /* background: url("../src/assests/Images/wave3.svg"); */
  background-size: cover;
}

.actImg {
  height: 4.5rem;
  align-self: center;
  justify-self: center;
  filter: drop-shadow(1px 1px 2px);
}

.paras {
  padding: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.parasHead {
  font-family: "Montserrat", sans-serif;
}

.parasContent {
  font-size: 15px;
  color: #232528;
}

.divisionsHr {
  opacity: 0.4;
}

.fa-bell-o {
  transform-origin: top;
  transition: all 0.4s;
  position: relative;
  cursor: pointer;
  margin-left: 0.3rem;
}

.fa-check-square-o {
  margin-left: 0.3rem;
}

.fa-bell-o:hover {
  animation: bellRings 0.4s 3 forwards;
}

@keyframes bellRings {
  0% {
    transform: rotateZ(0deg);
  }
  25% {
    transform: rotateZ(-10deg);
  }
  50% {
    transform: rotateZ(0deg);
  }
  75% {
    transform: rotateZ(10deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}

.notifCounts {
  display: flex;
  font-size: 11px;
  height: 14px;
  width: 14px;
  /* border: 1px solid; */
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: #d00606;
  color: white;
  font-family: monospace;
  position: absolute;
  top: -46%;
  right: -31%;
}

.notifsConatiner {
  margin-top: 0.2rem;
  min-height: 4rem;
  border-radius: 7px;
  border: 1px solid;
  border-color: #4a4a4a40;
  background: white;
  padding: 0rem 0.5rem;
  margin-bottom: 2rem;
}
.time {
  color: #636363;
  display: flex;
  justify-content: space-between;
}

.message {
  font-size: 17px;
  font-family: "Nunito", sans-serif;
}

.notif {
  border: 1px solid;
  padding: 0.4rem;
  border-radius: 7px;
  border-color: #80808075;
  background: #f9f9f9;
  margin: 0.6rem auto;
}

.markRead {
  cursor: pointer;
  color: #f50f0f;
  font-weight: 600;
  transition: all 0.1s;
  user-select: none;
}

.markAllRead {
  position: absolute;
  right: 0;
  cursor: pointer;
  color: #1a51ce;
  font-weight: 600;
  transition: all 0.1s;
  user-select: none;
}

.markRead:active {
  transform: scale(0.97);
}
.markAllRead:active {
  transform: scale(0.97);
}

.noNotifs {
  height: 100%;
  flex-direction: column;
  display: flex;
  align-items: center;
  font-size: 20px;
  justify-content: center;
  padding: 8px;
  font-family: "Nunito", sans-serif;
}

.noNotifs > p {
  font-weight: bold;
  display: flex;
  align-items: center;
}
.noNotifs > small {
  color: #4a4a4ad4;
}
