body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 768px) {
  .eapps-social-share-buttons-mobile-layout
    .eapps-social-share-buttons-mobile-button-open {
    position: fixed !important;
    bottom: 50px;
    left: auto;
    right: -8px !important;
    display: flex;
    justify-content: center;
    box-shadow: 2px 2px 7px 0px;
    align-items: center;
  }

  .eapps-social-share-buttons-mobile-layout
    .eapps-social-share-buttons-container {
    top: auto;
    top: initial;
    bottom: 47px !important;
    left: 0;
    -webkit-transform: perspective !important;
    transform: perspective(10px) !important;
  }
  .eapps-social-share-buttons-mobile-layout
    .eapps-social-share-buttons-mobile-button-open {
    background: #232528 !important;
  }
}
