@media (max-width: 1000px) {
  .detailsGrid {
    grid-template-columns: 1fr !important;
    width: 98% !important;
    margin-right: auto !important;
    margin-left: auto !important;
  }
  .inputGroup {
    margin-bottom: 15px !important;
  }
  .labelGroup {
    margin-bottom: 5px !important;
  }
  .SellerGrid {
    grid-template-columns: 1fr !important;
    width: 98% !important;
    margin-right: auto !important;
    margin-left: auto !important;
  }
  .gridForm {
    grid-template-columns: 1fr !important;
    margin: 0 0 !important;
    /* padding: 5px !important; */
    padding-bottom: 65px !important;
    column-gap: 0px !important;
  }
  .sellerLabel {
    margin-bottom: 0px !important;
  }

  .sellerDetails {
    margin-bottom: 10px !important;
  }
  .headingsGroup {
    width: 98% !important;
    margin-right: auto !important;
    margin-left: auto !important;
  }
  .submitContainer {
    width: 98% !important;
    margin-right: auto !important;
    margin-left: auto !important;
  }
  .container {
    border: none !important;
    /* margin-top: 25px !important; */
  }
  #files {
    margin-top: 8px !important;
    padding: 2px 2px !important;
  }
  .producyImagesContainer {
    width: 98% !important;
    margin-right: auto !important;
    margin-left: auto !important;
    padding: 3px !important;
  }
  .previewImage {
    height: 75px !important;
  }
  #preview > img {
    margin-right: 8px !important;
  }
}

.detailsGrid {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  border: 1px solid;
  padding: 15px;
  background: url("../src/assests/Images/blob_background2.png");
  background-size: cover;
  margin-bottom: 10px;
  border-radius: 5px;
  background: #f9f9f9;
}

.SellerGrid {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  border: 1px solid;
  padding: 15px;
  background: url("../src/assests/Images/blob_background.png");
  background-size: cover;
  margin-bottom: 10px;
  border-radius: 5px;
  /* background: #f9f9f9; */
}

.producyImagesContainer {
  width: 100%;
  border: 1px solid;
  padding: 15px;
  background: url("../src/assests/Images/blob_background2.png");
  background-size: cover;
  margin-bottom: 10px;
  border-radius: 5px;
  /* min-height: 20vh; */
  background: #f9f9f9;
}

.inputGroup {
  margin-bottom: 20px;
  padding: 10px 12px;
  border-radius: 3px;
  border: 0.1px solid;
  font-size: 17px;
  color: rgb(32, 32, 32);
}

.gridForm {
  display: grid;
  grid-template-columns: 1.4fr 1fr;
  column-gap: 20px;
  margin: 20px 30px;
}

@import url("https://fonts.googleapis.com/css2?family=Questrial&display=swap");

.labelGroup {
  margin-bottom: 20px;
  font-family: "Questrial", sans-serif;
  font-size: 17px;
}

.sellerLabel {
  margin-bottom: 20px;
  font-family: "Questrial", sans-serif;
  font-size: 17px;
}

@import url("https://fonts.googleapis.com/css2?family=Padauk&display=swap");

.headingsGroup {
  border: 1px solid;
  width: 100%;
  padding: 15px;
  font-size: 24px;
  color: rgb(0, 0, 0);
  font-family: "Padauk", sans-serif;
  margin-bottom: 5px;
  border-radius: 5px;
  background: #f9f9f9;
  /* background: linear-gradient(90deg, rgb(165, 180, 252), rgb(79, 70, 229)) */
}

.sellerDetails {
  font-size: 17px;
  color: black;
  text-decoration: none;
}

#preview {
  background: #f9f9f9;
  padding: 10px;
  border-radius: 5px;
  margin-top: 10px;
  border: 0.1px solid;
  color: grey;
  font-family: "Padauk", sans-serif;
  font-size: 18px;
  display: flex;
  align-items: center;
}

#preview > img {
  margin-right: 15px;
  border: 0.2px solid;
  border-radius: 2px;
}

.container {
  border: 1px solid;
  background: url("../src/assests/Images/wave_background2.png");
  background-size: cover;
  padding-top: 6px;
  width: 100%;
}

#files {
  /* marginBottom: "20px", */
  padding: 10px 12px;
  border-radius: 3px;
  font-size: 17px;
  color: rgb(32, 32, 32);
}

.previewImage {
  height: 100px;
  width: auto;
}

.imgPreviewDiv {
  position: relative;
  margin-right: 10px;
}

.removeImg {
    position: absolute;
    top: -5%;
    right: -8%;
    background: #e02c2c;
    color: white;
    border-radius: 50%;
    height: 15px;
    width: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    cursor: pointer;
    z-index: 1;
}

.removed{
  display: none;
}

#acceptanceCheckbox {
  height: 13px;
  width: 13px;
  transform: translateY(2px);
}
.headingsGroup > span {
  font-size: 16px;
}
.submitContainer {
  border: 1px solid;
  width: 100%;
  padding: 15px;
  color: rgb(0, 0, 0);
  font-family: "Padauk", sans-serif;
  margin-bottom: 5px;
  border-radius: 5px;
  background: #f9f9f9;
  /* background: linear-gradient(90deg, rgb(165, 180, 252), rgb(79, 70, 229)) */
}

.submitBtn {
  padding: 7px 12px;
  font-size: 15px;
  border-radius: 4px;
  border: 0.1px solid;
  cursor: pointer;
  background: url("../src/assests/Images/wave_background.png");
  background-size: cover;
  color: #f9f9f9;
  font-family: "Questrial", sans-serif;
}

.btnDiv {
  display: flex;
  justify-content: flex-end;
}