@media (max-width: 1000px) {
  .productGrid {
    grid-template-columns: repeat(2, 1fr) !important;
    column-gap: 8px !important;
    row-gap: 8px !important;
  }
  .ProductContainer {
    padding: 3px 10px 10px 10px !important;
    width: 100% !important;
    margin-bottom: 2rem;
  }
  .tile {
    height: 16rem !important;
  }
  .imgContianer {
    height: 55% !important;
  }
  .tileInfo {
    height: 48% !important;
    padding: 8px !important;
  }
  .pricings {
    font-size: 19px !important;
  }
  .productName {
    font-size: 13px !important;
  }
  .productDescription {
    font-size: 12px !important;
  }
  .containerTitle {
    font-size: 17.5px !important;
  }
  .viewMore {
    font-size: 14px !important;
  }
}   /* Media query ends here */

.ProductContainer {
  /* border: 0.2px solid; */
  padding: 10px 20px 20px 20px;
  border-radius: 5px;
  width: 96.5%;
  margin: auto;
  margin-bottom: 2rem;
  background-size: cover;
  position: relative;
}

.ProductContainer_1 {
  background: url("../src/assests/Images/wave.svg");
  background-size: cover;
}

.ProductContainer_1 .viewMore {
  color: #ffffff;
}
.ProductContainer_1 .containerTitle {
  color: #f9f9f9;
}

.ProductContainer_2 {
  background: url("../src/assests/Images/wave2.svg");
  background-size: cover;
}

.ProductContainer_2 .viewMore {
  color: #ffffff;
}
.ProductContainer_2 .containerTitle {
  color: #f9f9f9;
}

.ProductContainer_3 {
  background: url("../src/assests/Images/wave3.svg");
  background-size: cover;
}
.ProductContainer_3 .viewMore {
  color: #000000;
}
.ProductContainer_3 .containerTitle {
  color: #232528;
}

.ProductContainer::after {
  content: "";
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #368f8b1f;
  z-index: -1;
}

@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap");
.containerTitle {
  font-family: "Lato", sans-serif;
  font-size: 22px;
}

.viewMore {
  font-size: 16px;
  padding-bottom: 0.4px;
  border-bottom: 2px solid;
}

.tileTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px auto;
}

.productGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 20px;
}

.tile {
  height: 15rem;
  width: 100%;
  /* border: 2px solid; */
  background: #f9f9f9fd;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.05s;
}

.tile:hover {
  /* border: 0.5px solid; */
  box-shadow: 1px 1px 7px 0px;
}

.imgContianer {
  height: 65%;
  padding: 8px;
  /* border: 1px solid; */
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tileImg {
  height: 95%;
  width: auto;
}

.tileInfo {
  /* border: 1px solid; */
  height: 35%;
  padding: 10px;
  /* text-decoration: line-through; */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
}

@import url("https://fonts.googleapis.com/css2?family=Anek+Malayalam:wght@500&display=swap");
.pricings {
  font-size: 22px;
  font-family: "Anek Malayalam", sans-serif;
}

.MRP {
  text-decoration: line-through;
  color: rgb(165, 9, 9);
  margin-left: 3px;
}

.productName {
  font-size: 15px;
  letter-spacing: 0.2px;
}

.productDescription {
  font-size: 14px;
  letter-spacing: 0.2px;
  opacity: 0.7;
}
