@media (max-width: 1000px) {
  .detailsContainer {
    grid-template-columns: 1fr !important;
    padding-bottom: 5rem !important;
  }
  .imagesContainer {
    height: 64vh !important;
    padding: 1px !important;
  }
  .previewer {
    height: 71% !important;
  }
  .images {
    height: 26% !important;
  }
  .productDetailHeading {
    padding: 0.5rem !important;
  }
  .productDetailName {
    font-size: 21px !important;
  }
  .productDetailDesc {
    font-size: 15px !important;
  }
  .buyAt {
    font-size: 13px !important;
  }
  .priceDetails {
    font-size: 22px !important;
  }
  .detailMRP {
    font-size: 15px !important;
  }
  .detailReduction {
    font-size: 15px !important;
  }
  .aboutSellerContainer {
    margin-top: 1rem;
  }
  .aboutProductContainer {
    margin-top: 1rem;
  }
  .detailBox {
    padding: 8px 16px !important;
  }
}

.detailsContainer {
  display: grid;
  grid-template-columns: 0.9fr 1fr 0.6fr;
  column-gap: 1rem;
  /* padding: 1.5rem; */
  width: 100%;
  margin: auto;
  border: 1px solid;
  padding: 2% 1.5%;
  background: url("../src/assests/Images/peak_background.svg");
  background-size: cover;
}

.imagesContainer {
  width: 100%;
  /* border: 2px solid #6d6d6d; */
  height: 84vh;
  display: flex;
  flex-direction: column;
  padding: 3px;
  justify-content: space-between;
  align-items: center;
  border-radius: 7px;
}

.previewer {
  /* border: 1px solid; */
  width: 100%;
  height: 74%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 2px 8px 8px 0px rgb(31 38 135 / 37%);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border-radius: 7px;
  border: 1px solid rgba(255, 255, 255, 0.25);
}

#previngImage {
  height: 85%;
  width: auto;
  border-radius: 3px;
}

.images {
  /* border: 1px solid; */
  height: 24%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 1px 1px 21px -9px;
}

.allImages {
  height: 80%;
  /* border: 0.1px solid grey; */
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.1s;
}

.selected {
  padding: 4px;
  box-shadow: #2d2d2d 1px 1px 2px 1px;
}

.aboutProductContainer {
  width: 100%;
  /* border: 1px solid; */
  min-height: 10rem;
  background: whitesmoke;
  border-radius: 7px;
  height: max-content;
}

@import url("https://fonts.googleapis.com/css2?family=Mukta:wght@200&display=swap");
.productDetailHeading {
  box-shadow: 2px 8px 8px 0px rgb(31 38 135 / 37%);
  width: 100%;
  padding: 1rem;
  color: rgb(0, 0, 0);
  /* font-family: 'Mukta', sans-serif; */
  /* font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif; */
  border-radius: 5px;
  background: whitesmoke;
  margin: 0px auto;
  letter-spacing: 0.2px;
  /* background: linear-gradient(90deg, rgb(165, 180, 252), rgb(79, 70, 229)) */
}

@import url("https://fonts.googleapis.com/css2?family=PT+Sans&display=swap");
.productDetailName {
  font-family: "PT Sans", sans-serif;
  font-size: 28px;
  font-weight: bold;
}

@import url("https://fonts.googleapis.com/css2?family=Assistant:wght@300&display=swap");
.productDetailDesc {
  /* margin-top: 5px; */
  font-family: "Assistant", sans-serif;
  font-size: 17px;
}

.otherDetails {
  padding: 0rem 0rem;
  box-shadow: 2px 8px 8px 0px rgb(31 38 135 / 37%);
  padding: 1rem 1rem;
}

@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
.priceDetails {
  font-size: 27px;
  /* border: 1px solid; */
  font-family: "Roboto", sans-serif;
}

.detailMRP {
  text-decoration: line-through;
  color: rgb(165, 9, 9);
  margin-left: 0.6rem;
  font-size: 18px;
  font-family: "Roboto", sans-serif;
}

.detailReduction {
  color: #7e7e7e;
  margin-left: 0.85rem;
  font-size: 18px;
  font-family: "Roboto", sans-serif;
}

.buyAt {
  color: rgb(0, 88, 0);
  font-size: 15px;
  font-family: "Roboto", sans-serif;
  margin: 2px 0;
}

.addToCart {
  padding: 8px 15px;
  font-size: 16px;
  border-radius: 5px;
  border: 0.1px solid;
  color: #f9f9f9;
  cursor: pointer;
  align-self: center;
}

.wishlisted {
  background: #003e6c;
}
.notWishlisted {
  background: #0174c9;
}

.reportBtn {
  padding: 8px 15px;
  font-size: 16px;
  border-radius: 5px;
  border: 0.1px solid;
  color: #f9f9f9;
  cursor: pointer;
  align-self: center;
}

.notSubmitted {
  background: #d64550;
}

.submitted {
  background: #c50200;
}

.highlightsKey {
  margin-top: 3px;
  font-size: 16px;
  color: #343434;
}
.highlightsValue {
  font-size: 17px;
  color: #000000;
}

.otherProducts {
  margin-top: 5px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 10px;
}

.aboutSellerContainer {
  width: 100%;
  /* border: 1px solid; */
  height: max-content;
  /* background: url("../src/assests/Images/blob_background2.png"); */
  /* background: conic-gradient(from 230deg at 45% 40%, rgb(35, 37, 40), rgb(214, 69, 80)); */
  background: #f9f9f9;
  background-size: cover;
  border-radius: 7px;
  padding: 0.7rem;
}

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Upright&display=swap");

.sellerGeneralDetails {
  padding: 5px;
  font-size: 20px;
}

.sellerImgDiv {
  margin-top: 0.5rem;
  height: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

#sellerImage {
  height: 90%;
  /* border: 1px solid; */
  border-radius: 0.4rem;
}

.detailBox {
  /* border: 1px solid; */
  padding: 5px 16px;
}

.generalInfo {
  color: black;
  font-size: 18px;
}

#reportCategory {
  margin-top: 13px;
  padding: 10px 12px;
  border-radius: 3px;
  border: 0.1px solid;
  font-size: 17px;
  color: rgb(32, 32, 32);
  width: 100%;
}
