html,
body {
  height: 100%;
  width: 100%;
}

@media (max-width: 1000px) {
  .profileCont {
    grid-template-columns: 1fr !important;
  }

  .profileMenu {
    padding: 1rem 1rem !important;
    background: #232528 !important;
    width: 80% !important;
    box-shadow: 0px -1px 4px 0px !important;
    position: fixed;
    z-index: 2;
    top: 0;
    left: -80%;
    height: 100% !important;
  }

  #openMenu {
    display: flex !important;
  }

  #openAvtaar {
    display: flex !important;
  }

  .profileEdit {
    padding: 0.5rem 0.5rem 3.5rem 0.5rem !important;
    background: #f9f9f9;
  }
  .profileFrom {
    padding: 0.5rem 1rem !important;
  }
  .inputFields {
    width: 100% !important;
    background: #f9f9f9;
  }
  .inputPicture {
    background: transparent;
  }

  .sideMaterials {
    right: -80%;
    top: 0;
    position: fixed !important;
    width: 80% !important;
    z-index: 1;
    overflow-y: visible !important;
    padding: 8px !important;
  }

  .currentPicture {
    width: 100% !important;
  }

  .currentPicture > img {
    width: 60% !important;
  }

  .imgGrid {
    width: 72% !important;
  }

  .profileInputPicture {
    width: 100% !important;
  }

  .optionsHead {
    color: rgb(255 255 255) !important;
  }
  .menuListItem {
    color: aliceblue;
  }
  .menuListItem:hover {
    background: aliceblue !important;
    color: #232528;
  }
  .seeProfilePassword {
    right: 2% !important;
}
}

#menuOpened {
  display: none;
}

#avtaarOpened {
  display: none;
}

.profileCont {
  /* border: 1px solid; */
  height: 92.4vh;
  display: grid;
  grid-template-columns: 1fr 2.8fr 1.5fr;
  /* column-gap: 1px; */
  background: white;
}

.profileMenu {
  border-right: 1px solid #00000042;
  padding: 1.5rem 2rem;
  letter-spacing: 0.2px;
  transition: all 0.5s;
}

#openMenu {
  padding: 4px;
  top: 40%;
  right: 0;
  transform: translate(90%, -50%);
}

.menu {
  height: max-content;
  aspect-ratio: 1;
  background: #232528;
  position: absolute;
  border-radius: 15%;
  justify-content: center;
  align-items: center;
  display: none;
  transition: all 0.5s;
}

#openAvtaar {
  padding: 8px;
  top: 60%;
  left: 0;
  transform: translate(-90%, -50%);
}

.gridImg {
  height: 25px;
  border-radius: 4px;
}

.optionsHead {
  color: rgb(77 77 77);
  font-family: "Montserrat", sans-serif;
}

.menuList {
  list-style: none;
  margin-top: 0.5rem;
  padding: 0.2rem 0.4rem;
}

.menuListItem {
  font-weight: 600;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
  padding: 6px 13px;
  margin-bottom: 6px;
  border: 1px solid;
  border-radius: 5px;
  cursor: pointer;
  border-color: transparent;
  transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.1s;
}

.menuListItem:hover {
  background: rgb(242 243 247);
}

#saveChanges {
  position: absolute;
  display: block;
  width: 100%;
  padding: 5px;
  border-color: transparent;
  border-radius: 5px;
  font-family: "Montserrat", sans-serif;
  background: #0a6cff;
  color: white;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
  transform: all 0.1s;
}

#saveChanges:active {
  transform: scale(0.99);
}

#oldPassword {
  padding: 6px 22px 6px 8px;
  margin-bottom: 5px;
  width: 100%;
  border-radius: 3px;
  border-color: rgba(0, 0, 0, 0.226);
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  border: 0.1px solid;
}

.passwordLabel {
  position: relative;
}

.fa-eye {
  color: #00000059;
}

.fa-eye-slash {
  color: #000000b6;
}

.seePassword {
  position: absolute;
  font-size: 14px;
  top: 47%;
  transform: translateY(-50%);
  right: 2%;
  cursor: pointer;
}

.submitMessage {
  bottom: -190%;
  left: 0;
}

.passwordWarning {
  bottom: -48%;
  left: 0;
}

.warningMessage {
  position: absolute;
  width: 100%;
  color: red;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
}

.warning {
  border-color: red !important;
}

.profileEdit {
  padding: 1.5rem 2rem;
  border-right: 1px solid #00000042;
}

.headings {
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.3px;
}

.currentPicture {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.currentPicture > img {
  width: 50%;
  /* aspect-ratio: 1; */
}

.profileInputGroup {
  margin-top: 1rem;
}

.inputFields {
  width: 90%;
  padding: 7px 10px;
  border-radius: 3px;
  border-color: rgba(0, 0, 0, 0.226);
  font-family: "Montserrat", sans-serif;
  border: 0.1px solid;
  font-size: 15px;
  margin-top: 5px;
  font-weight: bold;
  resize: none;
}

.labelGroup {
  color: #000000b6;
}

.profileFrom {
  padding: 0.5rem 1.5rem;
}

.profilePasswordLabel {
  position: relative;
}

.seeProfilePassword {
  position: absolute;
  font-size: 14px;
  top: 56%;
  transform: translateY(-50%);
  right: 11%;
  cursor: pointer;
}

.sideMaterials {
  /* border: 1px solid; */
  height: 100%;
  padding: 1.5rem 0;
  background: #232528;
  box-shadow: 0px -1px 4px 0px;
  overflow-y: scroll;
  transition: all 0.5s;
}

.pictureInfo {
  margin-top: 2px;
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  color: white;
}

.imgGrid {
  width: 70%;
  margin: 2rem auto;
  border: 1px solid;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 5px;
  row-gap: 5px;
  padding: 0.4rem;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 2px 8px 8px 0px rgb(0 0 0 / 37%);
  backdrop-filter: blur(6px);
  border-radius: 7px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  justify-items: center;
  align-items: center;
}

.imgDivs {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 3px;
}

.gridImgItem {
  /* display: none; */
  width: 100%;
  height: 100%;
  border-radius: 3px;
  transition: all cubic-bezier(0.19, 1, 0.22, 1) 1.3s;
  cursor: pointer;
}

.orContainer {
  width: 55%;
  margin: 2rem auto;
  position: relative;
  /* border: 1px solid white; */
  background: white;
  opacity: 0.5;
  height: 0.2px;
}

.orDiv {
  position: absolute;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  background: #232528;
  top: -11px;
  left: 50%;
  transform: translateX(-50%);
  padding: 0px 6px;
}

.profileInputPicture {
  width: max-content;
  margin: auto;
}

.labelInput {
  color: white;
  font-family: "Questrial", sans-serif;
  font-size: 17px;
}

.inputPicture {
  width: 100%;
  color: white;
  font-family: "Montserrat", sans-serif;
}

.compressorAnchor {
  text-decoration: none;
  transition: all 0.1s;
}
.compressorAnchor:hover{
  text-decoration: underline;
}