@media (max-width: 1000px) {
  .bottomNav {
    display: flex !important;
  }
}

.bottomNav {
  position: fixed;
  bottom: 9px;
  left: 3%;
  display: none;
  height: 53px;
  border: 0.1px solid;
  /* box-shadow: 0.3px 0.3px 5px black; */
  width: 94%;
  /* background: #2e133a; */
  background: url("../src/assests/Images/wave_background.png");
  background-size: cover;
  align-items: center;
  justify-content: space-around;
  margin: auto;
  border-radius: 15px;
  z-index: 2;
}

.navItems {
  font-size: 23px;
  transition: all 0.2s;
  color: #ffffff;
  transition: all 0.2s;
}

.navTile {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 24%;
}

.navItemsClicked {
  opacity: 1;
  transform: translateX(-4px);
}

.navItemsClickedNotClicked {
  opacity: 0.8;
  transform: translateX(22px);
}

.tileText {
  color: #ffffff;
  transition: all 0.2s;
}

.tileTextNotClicked {
  transform: translateY(13px);
  opacity: 0;
  font-size: 12px;
}

.tileTextClicked {
  transform: translateY(0px);
  opacity: 1;
  font-size: 15px;
}

#bar {
  position: fixed;
  width: 15%;
  height: 5.3px;
  background: #368f8b;
  bottom: 40px;
  left: 5%;
  border-radius: 0px 0px 20px 20px;
  transition: all 0.2s;
}
