* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #f9f9f9;
}

a {
  text-decoration: none;
}

.navbar {
  width: 100%;
  height: 7.5vh;
  display: flex;
  /* background: linear-gradient(270deg, rgb(165, 180, 252), rgb(79, 70, 229)); */
  background: #232528;
  justify-content: space-between;
  padding-left: 10px;
  position: relative;
  z-index: 2;
}

@media (max-width: 1000px) {
  .main {
    padding-bottom: 2.5rem;
    padding-top: 0rem !important;
  }
  .loginSignup {
    display: none !important;
  }
  .title {
    font-size: 28px !important;
    color: #000000 !important;
  }
  .seachbar {
    position: absolute !important;
    top: 8% !important;
  }
  .titleAndTagline {
    width: 100% !important;
    text-align: center;
    color: #232528 !important;
  }
  #search {
    height: 44px !important;
    width: 100% !important;
    border: 0px solid !important;
    padding: 2px 50px 2px 18px !important;
    border-radius: 18px !important;
    border: 0.2px solid !important;
  }
  .seachbar {
    height: 48px !important;
    width: 100% !important;
    padding: 0px 10px !important;
    margin-top: 5px;
    background: #f9f9f9 !important;
    font-size: 15px !important;
  }
  .searchBtn {
    height: 45px !important;
    right: 10px !important;
    top: 1px !important;
    padding: 14px 12px !important;
    border-radius: 0px 18px 18px 0px !important;
    /* background: #2e133a !important; */
    background: url("../src/assests/Images/wave_background.png") !important;
    background-size: cover;
    font-size: 16px !important;
    color: #ffffff !important;
  }
  .navbar {
    padding-left: 0px !important;
    background: #f9f9f9;
    margin-bottom: 72px;
    position: unset;
  }
  .tagline {
    display: none;
  }

  #userProfile {
    display: block !important;
  }
  .alertMessage {
    color: #232528 !important;
  }
  .alertBox {
    text-shadow: 1px 1px 2px white !important;
  }
}
.titleAndTagline {
  height: 100%;
  width: 15%;
  color: #f9f9f9;
  padding: 2px 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@import url("https://fonts.googleapis.com/css2?family=Potta+One&display=swap");
.title {
  font-size: 24px;
  letter-spacing: 3px;
  font-family: "Potta One", cursive;
  margin-left: 4px;
  color: #f9f9f9;
}

@import url("https://fonts.googleapis.com/css2?family=Padauk&display=swap");
.tagline {
  font-size: 14px;
  font-family: "Padauk", sans-serif;
  margin-left: 4px;
  margin-top: -5px;
}

#search {
  height: 100%;
  width: 96%;
  padding: 2px 40px 2px 12px;
  font-size: 16px;
  letter-spacing: 0.5px;
  border-radius: 6px;
  justify-self: center;
  align-self: center;
}

.searchBtn {
  display: flex;
  height: 93.13%;
  padding: 11px 13px;
  font-size: 18px;
  border-radius: 0px 6px 6px 0px;
  position: absolute;
  right: 16px;
  display: flex;
  top: 2px;
  width: 43px;
  border: none;
  background: #d64550;
  /* display: flex; */
  color: #ffffff;
  /* justify-content: center; */
  cursor: pointer;
  transition: all 0.05s;
}
.searchBtn:active {
  transform: scale(1.04);
}

.seachbar {
  position: relative;
  height: 75%;
  width: 53%;
  align-self: center;
  justify-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginSignup {
  width: 32%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.navbtn {
  text-decoration: none;
  color: #f9f9f9;
  background: #232528;
  padding: 4px 8px;
  font-size: 18px;
  font-family: "Padauk", sans-serif;
  border-radius: 7px;
  transition: all 0.2s;
  letter-spacing: 0.7px;
  border: 1px solid transparent;
}

.navbtn:hover {
  /* border: 1px solid #f9f9f9; */
  /* background: #f9f9f9; */
  /* color: #232528; */
  transform: translate(-0px, -4px);
  text-decoration: underline;
}

#userProfile {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  position: absolute;
  right: 2.5%;
  top: 1%;
  display: none;
}

/* Alert CSS */
.alertBox {
  min-height: 60px;
  padding: 0 15px;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  font-size: 16px;
  width: 95%;
  position: fixed;
  transform: translateX(-50%);
  left: 50%;
  top: 1%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 2;
  text-shadow: 1px 1px 2px black;
  /* letter-spacing: 3px; */
  /* font-family:Georgia, 'Times New Roman', Times, serif; */
}

#dismissMessage {
  justify-self: flex-end;
  display: flex;
  align-items: center;
  justify-content: center;
}
#dismissMessage > img {
  height: 30px;
  width: 30px;
  cursor: pointer;
  border-radius: 5px;
  padding: 3px;
  border: 1px solid transparent;
  transition: all 0.1s;
}

#dismissMessage > img:hover {
  border: 1px solid;
}

.success {
  color: rgb(0, 68, 0);
}

.alertMessage {
  letter-spacing: 0.3px;
  color: #ffffff;
}

.main {
  padding-top: 1.5rem;
}

.light {
  color: #232528;
}

.lightA {
  color: #032e6e;
}

.dark {
  color: #f9f9f9;
}

.darkA {
  color: #a8bbf8;
}